.item-filter {
    display: flex;
    align-self: flex-start;
    align-items: center;
    background-color: var(--color-white-bg);
    width: 100%;
    padding: 10px;
    margin-bottom: 5px;
}
.item-filter img {
    width: 50px;
}
.item-filter .box-item-filter {
    margin-right: 5px;
}
.item-filter .box-item-filter .title {
    font-size: 20px;
    color: var(--color-black);
    font-weight: bold;
}
.item-filter .box-item-filter .subtitle {
    font-size: 18px;
    color: var(--color-green);

}
.notfound {
    font-size: 20px;
    text-align: center;
    color: var(--color-red);
}
