body #splach {
    min-height: 100vh;
    width: 100%;
    background: url('../../images/bg.jpg') no-repeat;
    background-size: cover;
}
body #splach .container {
    background-color:#f1f1f1e8; 
    /* #f1f1f1cd; */
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 10px;
}
body #splach .container img {
    width: 350px;
    animation: shirk 3s infinite ease;
}
body #splach .container .container-title {
    text-align: center;
    padding: 10px;
}
body #splach .container .container-title h1{
color: var(--color-green);
}
body #splach .container .container-title p {
    font-size: 25px;
    color: var(--color-black);
    font-weight: bold;
}
body #splach .container .container-title button {
width: 100%;
background-color: var(--color-green);
color: var(--color-white);
border-radius: 25px;
margin-top: 10px;
box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
body #splach .container .container-title button:hover {
    background-color: var(--color-red);
}

@keyframes shirk{
    0% {
    transform: translateY(0px);
    }
    50% {
        transform: translateY(7px);

    }
    100% {
        transform: translateY(-7px);
    }
}

@media (max-width:767px){
    body #splach .container img {
        width: 250px;
    }
    body #splach .container {
        flex-direction: column;
        justify-content: center;
    }
}