section {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    max-height: 100vh;
    background-color: rgba(0,0,0,0.3);
    display: flex;
    justify-content: center;
    align-items: center;
}
section .box-filter {
    max-height: inherit;
    width: 60%;
    padding: 10px;
    background-color: var(--color-white);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
section .box-filter .icon-close {
    color: var(--color-red);
    cursor: pointer;
    align-self: flex-start;
}
section .box-filter .result {
    width: 100%;
    max-height: 60vh;
    overflow: auto;
}
section .box-filter .result::-webkit-scrollbar {
background-color: var(--color-green);
}
section .box-filter .result::-webkit-scrollbar-thumb {
background-color: var(--color-black);
}
section .box-filter .btn {
    background-color: transparent;
    border: 1px solid var(--color-green);
    color: var(--color-green);
    margin-top: 5px;
    align-self: flex-start;
}
section .box-filter .btn:hover {
    color: var(--color-white);
    background-color: var(--color-green);
}
@media (max-width:604px){
    section .box-filter{
        width: 98%;
    }
}