 .search {
    min-height: 100vh;
    width: 100%;
    background: url('../../images/pattern.svg');
    background-color: var(--color-white-bg);
    display: flex;
    align-items: center;
    justify-content: center;
}
.search .box {
    padding: 10px;
    text-align: center;

}
.search .box .title {
    font-size: 20px;
    margin-top: 5px;
    color: var(--color-black);
    font-weight: bold;
}
.search .box .title::after {
content: '-';
color: var(--color-red);
}
.search .box .title::before {
    content: '-';
    color: var(--color-red);
    }
.search .box img {
    width: 350px;
}
.search .box .line {
    height: 2px ;
    width: 100%;
    background-color: var(--color-green);
}
.search .box .box-input {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    border: 2px solid #ccc;
    padding: 10px;
    background-color: var(--color-white);
    border-radius:25px ;
    margin-top: 5px;
    row-gap: 5px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

}
.search .box input {
    border: none;
    outline: none;
    width: 100%;
    direction: rtl;
    font-family: "Almarai", serif;
    color: var(--color-green);
    font-weight: bold;
    background-color: transparent;
    
}
.search .box .box-input .icon {
    color: var(--color-green);
    margin-left: 5px;
}
.search .box input::placeholder{
    direction: rtl;
}
.search .box .btn {
    width: 100%;
    background-color: var(--color-green);
    border-radius: 25px;
    color: var(--color-white);
    margin-top: 5px;
}
.search .box .btn:hover {
    background-color: var(--color-red);
}
.marquee {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    animation:marquee 25s infinite linear;
    white-space: nowrap;
    padding: 5px 0;
  
}
@keyframes marquee {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(100%);
    }
}
@media (max-width:767px){
    .search .box img {
        width: 90%;
    }
}